@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.2.1/css/bootstrap.min.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navigation-buttons {
  display: flex;
  flex-direction: column;
  height: 30%;
  position: relative;
  z-index: 1;
}

.back-button {
  z-index: 1;
  width: 43px;
  height: 25.5px;
  cursor: pointer;
}

.back-button-svg {
  fill: #EFEFEF;
}

.back-button-svg:hover {
  fill: #0071BC;
}

.next-button {
  z-index: 1;
  width: 43px;
  height: 25.5px;
  position: relative;
  margin-top: 20px;
  cursor: pointer;
}

.next-button-svg {
  fill: #EFEFEF;
}

.next-button-svg:hover {
  fill: #0071BC;
}

.disabled {
  fill: #666666;
  cursor: default;
}

.disabled:hover {
  fill: #666666 !important; 
}

.current-page {
  margin-top: 20px;
  color: #FFFFFF;
  font-size: 22.5px;
  position: relative;
  left: -5px;
  cursor: default;
}
.text-description {
    color: #FFFFFF;
    font-size: 22px;
    font-family: 'lato';
    position: absolute;
    top: 16.3vh;
    left: 7.1vw;
    width: 23.4vw;
}

.text-module {
    font-size: 42.1pt;
}

.text-section {
    font-size: 28.1pt;
}

.text-section-subtitle {
    font-size: 28.1pt;
}

.text-description-body {
    font-size: 22.5pt;
    overflow-y: scroll;  
    max-height: 50vh;
    overflow-wrap: break-word;
    max-width: 23.4vw;
}

.left-section {
	background-color: #19191A;
	width: 100%;
	height: 100%;
	position: relative;
}

.navigation-arrows {
	position: absolute;
	bottom: 0;
	right: 0;
	margin-bottom: 20px;
	margin-right: 20px;
}

.left-text {
	position: relative;
}
.animationBox {
	position: relative;
}

.controlButton {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-top: 10px;
}

.replayButton {
	height: 50px;
	width: 50px;
	fill: #2343F5;
	cursor: pointer;
	margin-right: 10px;
}

.replayButton:hover {
	fill: #566EF7;
}

.pauseButton {
	height: 50px;
	width: 50px;
	fill: #2343F5;
	cursor: pointer;
	margin-left: 10px;
}

.pauseButton:hover {
	fill: #566EF7;
}
.image-slider {
	height: 100%;
	width: 100%;
}

.image-after {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 50%;
  overflow: hidden;
}

.image-container {
	width: 100%;
	height: 100%;
}

.open-modal {
	position: relative;
	top: 10vh;
	left: 10vw;
	width: 24vh;
	height: 3vh;
	background-color: white;
	z-index: 2;
	cursor: pointer;
	border-radius: 5px;
}

.open-modal:hover {
	background-color: grey;
}

.popup-image {
	width: 90%;
	height: 90%;
}

.popup-content {
	display: flex;
	flex-direction: column;
	white-space: pre-line;
}

.popup-dialog {
	max-width: 80vw !important;
	width: 80vw !important;
	max-height: 80vh !important;
}


.content-slider {
  position: relative;
  height: 100%;
  width: 100%;
  overflow: visible;
}

.slider-icon {
	position: relative;
	height: 50px;
	width: 50px;
	top: 10%;
	left: -23px;
	cursor: col-resize;
}

.title {
	position: relative;
	font-weight: 500;
	font-size: 3vmin;
	text-transform: capitalize;
	letter-spacing: 0.01em;
	line-height: 1.2;
	color:#fff;
	margin: 3vmin 5vmin 3.5vmin 5vmin;
	top: 30vh;
}

.theme {
	position: relative;
	top: 20vh;
	font-weight: 900;
	font-size: 1.7vmin; 
	text-transform:uppercase;
	font-family:Poppins;
	letter-spacing: 10px;
	margin: 3vmin 5vmin 3.5vmin 5vmin;
	color: lightblue;
}

.images {
	height: 100vh;
	width: 60%;
	overflow: hidden;
	position: relative;
}

.text-content {
	position: relative;
	text-align: center;
	background-color: black;
	height: 100vh;
	width: 40%;
	text-align: left;
}

.description {
	position: relative;
	top: 30vh;
	font-size: 2vmin;
	line-height: 1.7;
	font-weight: 300;
	color:#fff;
	margin: 3vmin 5vmin 3.5vmin 5vmin;
	white-space: pre-line;
	
}

.slider-list {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
  	width: 100%;
  	overflow: visible;
}

.list-item {
	position: relative;
	height: 100vh;
  	width: 100%;
  	top: 24.7vh;
    left: 6.6vw;
    overflow: visible;
}

.old {
	position: relative;
	height: 100%;
  	width: 100%;
  	overflow: hidden;
  	display: flex !important; 
  	flex-direction: row;
}

.new {
	position: relative;
	height: 100%;
  	width: 100%;
  	overflow: hidden;
  	display: flex !important; 
  	flex-direction: row;
  	overflow: visible;
}

.vertical-separator {
	position: absolute;
	top: 0;
  	bottom: 0;
	height: 100%;
	width: 5px;
	background: grey;
	z-index: 1;
}
.HamMenu {
	position: absolute;
	right: 8vw;
	top: 5vh;
	color: white;
	display: flex;
	flex-direction: column;
	vertical-align: top;
	text-align: center;
	cursor: pointer;
}

.modal-content {
	background-color: rgba(0, 113, 188, 0.7);
}

.modal-header {
	border-bottom: none;
}

.modal-title {
	opacity: 1 !important;
	color: white;
}

.modal-body {
	opacity: 1 !important;
	color: white;
}

button {
	color: white !important;
}

.module-expand-button {
	display: flex;
	flex-direction: row;
}

.module-itself {
	margin-left: 10px;
	cursor: pointer;
	margin-bottom: 15px;
	transition: 1s;
}

.module-section {
	margin-left: 40px;
	margin-bottom: 20px;
	cursor: pointer;
	transition: 0.3s;
}

.module-section.collapsed {
	-webkit-transform-origin: top;
	        transform-origin: top;
	-webkit-transform: scaleY(0);
	        transform: scaleY(0);
}

.module-menu-item.collapsed {
	height: 40px;
}

.module-menu-item {
	transition: 1s;
}

.expand-button-svg {
	height: 30px;
	width: 30px;
	fill: white;
	cursor: pointer;
}
.right-section {
	background-color: #27272B;
	width: 100%;
	height: 100%;
	position: relative;
	overflow: hidden;
}
.scroll-general {

}

.scroll-general:hover {
	text-shadow: 1px  1px     #FFFF66, 
    			-1px  1px     #FFFF66, 
    			 1px -1px     #FFFF66, 
    			-1px -1px     #FFFF66,
     			 1px  1px 5px #555; 
}

.ScrollNav {
	position: absolute;
	top: 0;
	right: -15.5vw;
	height: 100vh;
	width: 20vw;
	z-index: 1050;
	transition: all 500ms;
}

.ScrollNav.active {
	-webkit-transform: translateX(-15vw);
	        transform: translateX(-15vw);
}

.change-section {
	position: relative;
	margin-left: 6vw;
	margin-right: 3.5vw;
	margin-top: 3vh;
	height: 20vh;
}

.ScrollArea {
	overflow-y: scroll;
	height: 100%;
	width: 100%;
	padding-top: calc(30vh - 80px / 2.0);
	padding-bottom: calc(50vh - 14.1vh + 20vh);
	z-index: 2;
}

.ScrollArea::-webkit-scrollbar { width: 0 !important }

.nav-selected {

}

.nav-selected:hover {

}

.pointer {
	position: absolute;
	height: 3px;
	width: 8vw;
	top: 50vh;
	background-color: grey;
}

li {
	text-align: center;
}

ul {
	list-style-type: none;
	height: 100%;
	
}

.background-nav {
	background-color: #005F93;
	height: 100vh;
	width: 16.5vw;
	position: absolute;
	top: 0;
	right: 0;
	opacity: 0.93;
	z-index: -1;
	transition: all 500ms;
	-webkit-transform-origin: right;
	        transform-origin: right;
}

.background-nav.active {
	
}

.page-selected {
	color: #FFFFFF !important;
}

.page-selected.page-number {
	background-color: #005F93;
	opacity: 0.93;
}

.page-number {
	position: relative !important;
	width: 3.5vw;
	padding-left: 0.5vw;
	margin-bottom: 14.1vh;
}

.page-title {
	position: relative !important;
	padding-left: 1vw;
	transition: 0.5s;
	font-size: 1.2vw;
	max-width: 15vw;
}

.page-row {
	z-index: 1;
	color: #808080;
	position: relative;
	cursor: pointer;
	display: flex;
	flex-direction: row;
	font-size: 2vw;
	z-index: 2;
	transition: 0.5s;
}

.current-section {
	margin-top: 10px;
	opacity: 0.3;
	vertical-align: top;
	text-align: center;
	color: white !important;
}

.actual-section {
	margin-left: 2vw;
	margin-right: 1vw;
	opacity: 1;
}

.change-section-title {
	color: white;
	font-size: 1.5vw;
}

.section-change-buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 10vh;
  position: relative;
  z-index: 1;
  margin-top: 1vh;
  
}

.section-back-button {
  z-index: 1;
  width: 15px;
  height: 15px;
  cursor: pointer;
  font-size: 0.7vw;
  vertical-align: top;
  text-align: center;
}

.section-back-button-svg {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  fill: #EFEFEF;
}

.section-back-button-svg:hover {
  fill: #0071BC;
}

.section-next-button {
  z-index: 1;
  width: 15px;
  height: 15px;
  cursor: pointer;
  font-size: 0.7vw;
  vertical-align: top;
  text-align: center;
}

.section-next-button-svg {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  fill: #EFEFEF;
}

.section-next-button-svg:hover {
  fill: #0071BC;
}

.disabled {
  fill: #666666;
  cursor: default;
}

.disabled:hover {
  fill: #666666 !important; 
}


.modal-backdrop {
  transition: 0.5s;
  opacity: 0;
  pointer-events: none
}

.scroll-app {
  overflow: visible;
  position: relative;
}

.left-side {
  width: 35vw;
  height: 100vh;
}

.right-side {
  width: 65vw;
  height: 100vh;
}

.scroll-nav {
  position: absolute;
  height: 0px;
  width: 0px;
  z-index: 1;
}

.content-right {
  position: absolute;
  width: 100%;
  height: 100%;
}

.App {
  display: flex;
  flex-direction: horizontal;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

